import React from 'react'
import {
    Card
  } from 'reactstrap';
import axios from 'axios'
import LogoContainer from '../components/LogoContainer'
import {CopyToClipboard} from 'react-copy-to-clipboard';


class Cards extends React.Component {
    constructor(props) {
      super(props);
  
      this.onChange = {
        name1: this.handleChange.bind(this, 'name1')
      }
  
      this.state = {
        valor1: '',
        valor2: '',
        totalUSD: '',
        totalEURO: ''
      };
      this.getMoney = this.getMoney.bind(this)
    };
    handleChange(name, event) {
      this.getMoney(name, event.target.value);
      this.setState({copied: ''})
    };
  
    onCopy = () => {
      this.setState({copied: 'Copiado!'});
    };

    getMoney(name,e) {
      let self = this;
      function round(value, decimals) {
        return Number(Math.round(value+'e'+decimals)+'e-'+decimals);
      }
        axios.get('https://free.currconv.com/api/v7/convert?apiKey=b4e8126aaeefdc638d79&q=EUR_ARS,USD_ARS')
          .then(function (response) {
              let EURtoARS = response.data.results.EUR_ARS.val
              let totalEUR = EURtoARS*e
              let USDtoARS = response.data.results.USD_ARS.val
              let totalUSDs = USDtoARS*e
              self.setState({
                totalEURO: '$ '+round(totalEUR,2),
                totalUSD: '$ '+round(totalUSDs,2)
              })
          })
          .catch(function (error) {
            // handle error
            console.log(error);
          });
    }  


render(){

    return (
      <div className="section">
        <div className="full-height">
          <div className="absolute-center">
            <div className="container">
              <div className="row">
          <LogoContainer />
          <Card className="col-12 col-md-12 col-lg-12 mt-5 border-0 bg-dark">
              <div className="row">
                  <div className="col-sm-12 col-md-12 col-lg-6 mx-auto pt-3  mx-auto">
                  <div className="input-group">
                    <div className="input-group-prepend">
                        <span className="input-group-text bg-input-green" id="basic-addon">
                        <i className="fa fa-hand-o-right" aria-hidden="true"></i>
                        </span> 
                    </div>
                   <input type="tell" pattern="[0-9]*" className="form-control bg-input-green text-dark" onChange={ this.onChange.name1 }  placeholder="Ingresar monto en USD o EURO"/>
                </div>
                 </div>     
                 </div>     

                 <div className="row mx-auto">

                  <div className="col-sm-12 col-md-12 col-lg-6 mx-auto mt-3">

                  <div className="input-group w-100">
                    <div className="input-group-prepend bg-input-white">
                        <span className="input-group-text color-green" id="basic-addon">
                        <i className="fa fa-usd" aria-hidden="true"></i>
                        </span> 
                    </div>
                   <input type="text" className="form-control bg-dark border-ahora totalCur text-white" value={this.state.totalUSD} disabled placeholder="Pesos ARS a USD"/> 
                    <CopyToClipboard
            onCopy={this.onCopy}
            text={this.state.totalUSD}>
            <button className="btn btn-cambio">
                <i className="fa fa-clipboard" aria-hidden="true"></i>
              </button>
          </CopyToClipboard>
                </div>   
                </div>   

                <div className="col-sm-12 col-md-12 col-lg-6 mx-auto mt-3">
                <div className="input-group w-100">
          <div className="input-group-prepend bg-input-white">
                        <span className="input-group-text color-green" id="basic-addon">
                        <i className="fa fa-euro" aria-hidden="true"></i>
                        </span> 
                    </div>
                   <input type="text" className="form-control bg-dark border-ahora totalCur text-white" value={this.state.totalEURO} disabled placeholder="Pesos ARS a EUROS"/> 
                    <CopyToClipboard
            onCopy={this.onCopy}
            text={this.state.totalEURO}>
            <button className="btn btn-cambio">
                <i className="fa fa-clipboard" aria-hidden="true"></i>
              </button>
          </CopyToClipboard>
                </div>

                
                <p className="text-center text-white" id="copiado"> {this.state.copied} </p>  

                  </div>
            </div>
        <p className="text-muted small text-center"><i className="fa fa-copyright" aria-hidden="true"></i> Hecho con React por <a href="https://www.instagram.com/swing_av" className="text-muted"> Agencia Swing</a></p>
          </Card>
        </div>
        </div>
        </div>
        </div>
        </div>
        )
}
}
export default Cards